<template>
  <div class="page-index">
    <div class="no-load" v-if="noLoad">加载中</div>
    <img class="page-bg" src="@/assets/images/bg.jpg" />
    <img class="page-logo" src="@/assets/images/logo.png" />
    <img class="page-title" src="@/assets/images/title.png" />
    <lottery></lottery>
    <winner-dialog></winner-dialog>
    <activity-dialog></activity-dialog>
    <register-dialog :config="registerConfig"></register-dialog>
    <ImgCodeDialog></ImgCodeDialog>
  </div>
</template>
<script>
import Lottery from "@/components/Lottery";
import WinnerDialog from "@/components/WinnerDialog";
import ActivityDialog from "@/components/ActivityDialog";
import RegisterDialog from "@/components/RegisterDialog";
import ImgCodeDialog from "@/components/ImgCodeDialog";

export default {
  components: {
    Lottery,
    WinnerDialog,
    ActivityDialog,
    RegisterDialog,
    ImgCodeDialog
  },
  data() {
    return {
      registerConfig: {
        isShow: false
      },
      noLoad: true
    };
  },
  created() {},
  mounted() {
    setTimeout(() => {
      this.noLoad = false;
    }, 2000);
  },
  methods: {}
};
</script>
<style lang="stylus">
.page-index {
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;

  // @media screen and (min-device-width: 900px) {
  // width: 900px;
  // }
  .page-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: -1;
  }

  .page-title {
    width: 88%;
    display: block;
    margin: 0 auto 2%;
  }
}

.page-logo {
  position: absolute;
  left: 10px;
  top: 10px;
  width: 18%;
  z-index: -1;
}

.no-load {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: #000;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  font-size 16px;
}
</style>