  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;

  // @media screen and (min-device-width: 900px) {
  //   width: calc(900px * 0.8);
  // }
}

.register-dialog {
  width: 100%;
  display: block;
  margin: 0 auto;
}

.register-info {
  width: 100%;
  position: absolute;
  left: 0;
  top: 35px;

  // @media screen and (min-device-width: 900px) {
  //   top: 15px;
  // }

  .title {
    width: 100%;
    padding: 0 5%;
    font-size: 15px;

    // @media screen and (min-device-width: 900px) {
    //   font-size: 5px;
    // }

    text-align: center;
    color: #f8d15b;
  }

  .mobile-input {
    width: 100%;
    padding: 0 20px;
    margin-top: 5px;

    // @media screen and (min-device-width: 900px) {
    //   padding: 0 5px;
    //   margin-top: 5px;
    // }

    img {
      width: 100%;
    }

    input {
      padding: 0 20px 0 30px;

      // @media screen and (min-device-width: 900px) {
      //   padding: 0 10px 0 10px;
      // }
    }
  }

  .code-box {
    width: 100%;
    padding: 0 20px;
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    // @media screen and (min-device-width: 900px) {
    //   padding: 0 5px;
    //   margin-top: 3px;
    // }

    .code-input {
      flex: 1;
      margin-right: 15px;

      // @media screen and (min-device-width: 900px) {
      //   margin-right: 8px;
      // }

      img {
        width: 100%;
      }

      input {
        padding: 0 10px;

        // @media screen and (min-device-width: 900px) {
        //   padding: 0 3px;
        //   top: -2%;
        // }
      }
    }

    .code-btn {
      width: calc((176px / 2));

      // @media screen and (min-device-width: 900px) {
      //   width: calc((70px / 2));
      // }
    }
  }

  .register-tips {
    color: red;
