    // }
  }

  .submit {
    width: 113px;
    display: block;
    margin: 5px auto 0;

    // @media screen and (min-device-width: 900px) {
    //   width: 50px;
    //   margin: 2px auto 0;
    // }
  }
}

.close {
  position: absolute;
  right: 10px;
